.home {
    height: 80vh;

    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: left;
    background-size: auto;
    background-repeat: no-repeat;
    background-position: center center;
    background-origin: border-box;
    object-fit: cover;
   




}

.headercontainer {
    width: auto;
    margin-left: 30px;

}

.headercontainer h1 {
    justify-content: center;
    font-size: 70px;
    font-weight: bold;
    color: white;

}

.headercontainer p {
    margin-top: 20px;
    font-size: 45px;
    font-weight: lighter;
    color: wheat;
}

.headercontainer button {
    color: black;
    margin-top: 15px;

    height: 40px;
    text-decoration: none;
    font-weight: bold;
    font-size: 20px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
    padding: 0px 9px;
}

.headercontainer button:hover {
    background-color: rgb(239, 48, 48);
    transition: 1s;
}

@media only screen and (max-width:670px) {
    .home {
        justify-content: center;
    align-items: center;

        height: 80vh;
        background-size: 100% 100%;




    }

    .headercontainer {
        margin-left: 0;
        border-radius: 10px;
        width: 70%;
        margin-top: 200px;

        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        /* background-color: rgb(255,255,255); */
        background-color: rgba(0, 0, 0, 0.4);
        /* filter: blur(1px); */

        /* background-size: cover;
    background-position: center;
    background-repeat: no-repeat; */


    }

    .headercontainer h1 {
        font-size: 20px;
        height: 15px;
        margin: 15px 0px;
        color: white;
    }

    .headercontainer p {
        font-size: 20px;
    }

    .headercontainer button {
        margin-top: 10px;

        height: 30px;

        font-weight: bold;
        font-size: 15px;


        border-radius: 5px;
        padding: 0px 6px;
        margin-bottom: 10px;
    }

}